<div class="companyCardMain" v-if="double"><div class="companyCard collapsed row" data-bs-toggle="collapse" :data-bs-target="'#collapse-1-'+company.id"><div class="companyId col-12 col-md-3"></div><div class="companyName col-11 col-md-8">{{company.name}}<b class="redColor" v-if="company.rek_sub || company.rek_tech"> (Есть несоответствия)</b><b class="redColor"> (Найдены дубликаты)</b></div><div class="col-1 arrowCollapse"><img src="@/assets/images/arrowCollapse.svg"></div></div><hr><div class="collapse" :id="'collapse-1-'+company.id"><div class="companyCardMatched row"><div class="companyId col-12 col-md-3">ID: {{company.id}}</div><div class="companyName col-9 col-md-6">{{company.name}}</div><div class="col-3 col-md-3 d-flex justify-content-end align-content-center"><b class="showMore"><router-link :to="'/company/'+company.id">Подробнее</router-link></b><img class="arrowLink" src="@/assets/images/arrowLink.svg"></div></div><div class="companyCardMatched row" v-for="double in company.duble.split(',')"><div class="companyId col-12 col-md-3">ID: {{double}}</div><div class="companyName col-9 col-md-6">{{company.name}}</div><div class="col-3 col-md-3 d-flex justify-content-end align-content-center"><b class="showMore"><router-link :to="'/company/'+double">Подробнее</router-link></b><img class="arrowLink" src="@/assets/images/arrowLink.svg"></div></div><hr></div></div><div class="companyCardMain" v-else><div class="companyCardMatched row"><div class="companyId col-12 col-md-3">ID: {{company.id}}</div><div class="companyName col-9 col-md-6">{{company.name}}<b class="redColor" v-if="company.rek_sub || company.rek_tech"> (Есть несоответствия)</b></div><div class="col-3 col-md-3 d-flex justify-content-end align-content-center"><b class="showMore"><router-link :to="'/company/'+company.id" v-if="!company.id_company">Подробнее</router-link><router-link to="#" v-else>Подробнее</router-link></b><img class="arrowLink" src="@/assets/images/arrowLink.svg"></div></div><hr></div>